    UpdatePenComponent.prototype.ionViewDidLoad = function () {
        var _this = this;
        this.platform.ready().then(function () {
            _this.updateUserInfo();
            _this.updateBlackWhiteList();
            _this._ble.enable();
            _this.dependencies = _this.navParams.get('dependencies') || {};
            _this.dpDevice = { name: '' };
            if (_this.dependencies && _this.dependencies.pen && !_this.dependencies.clinic) {
                if (_this.dpDevice) {
                    _this.dpDevice.name = _this.dependencies.pen.serialNumber;
                    _this.dpDevice.id = _this.dependencies.pen.serialNumber;
                    _this.dpDevice.serialNumber = _this.dependencies.pen.serialNumber;
                }
                else {
                    _this.dpDevice = {
                        id: _this.dependencies.pen.serialNumber,
                        name: _this.dependencies.pen.serialNumber,
                        serialNumber: _this.dependencies.pen.serialNumber
                    };
                }
                _this.initConnectionChecker();
                _this._ble.scan(_this.dpDevice || {}, function (resp) {
                    // this._ble.discoverableSec(60);
                    _this._ble.connect(_this.dpDevice, _this.connected, false);
                }, _this._ble.stopScan);
            }
            else {
                _this.startScanning();
            }
        });
    };
    UpdatePenComponent.prototype.startScanning = function (existingDevice) {
        var _this = this;
        this.initConnectionChecker();
        this._ble.stopScan();
        this._ble.scan(existingDevice || {}, function (resp) { return _this.scanSuccess(resp); }, this.fail);
    };
    UpdatePenComponent.prototype.selectDevice = function (address, device) {
        var _this = this;
        if (device) {
            this.pairingDevice = JSON.stringify(device);
        }
        var alert = this.alertCtrl.create({
            title: 'Connect',
            message: 'Do you want to connect with?',
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: function () {
                        console.log('Cancel clicked');
                    }
                },
                {
                    text: 'Connect',
                    handler: function () {
                        _this._ble.connect(device, _this.connected, _this.fail);
                    }
                }
            ]
        });
        alert.present();
    };
    UpdatePenComponent.prototype.disconnect = function () {
        var _this = this;
        var alert = this.alertCtrl.create({
            title: 'Disconnect?',
            message: 'Do you want to Disconnect?',
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: function () {
                        console.log('Cancel clicked');
                    }
                },
                {
                    text: 'Disconnect',
                    handler: function () {
                        _this._ble.disconnect(_this.dpDevice, _this.disconnected, _this.fail);
                    }
                }
            ]
        });
        alert.present();
    };
    UpdatePenComponent.prototype.alertOk = function (text) {
        var alert = this.alertCtrl.create({
            title: 'Message',
            message: text,
            buttons: [
                {
                    text: 'Ok',
                    role: 'cancel',
                    handler: function () {
                        console.log('Cancel clicked');
                    }
                }
            ]
        });
        alert.present();
    };
    UpdatePenComponent.prototype.readFromDevice = function (address, serviceUUID, characteristicUUID, type) {
        var _this = this;
        this._ble.read(address, { serviceUUID: serviceUUID, characteristicUUID: characteristicUUID }, type, function (done) {
            _this.success(done, 'readFromDevice ' + _.get(_.find(_this.deviceInterface.characteristics, ['characteristic', characteristicUUID]), 'name'));
            setTimeout(function () { return _this.alertOk(JSON.stringify(done)); }, 200);
        }, this.fail);
    };
    UpdatePenComponent.prototype.writeToDevice = function (address, serviceUUID, characteristicUUID, rawData) {
        var _this = this;
        this._ble.write(address, { serviceUUID: serviceUUID, characteristicUUID: characteristicUUID }, rawData, function (done) {
            _this.success(done, 'writeToDevice ' + _.get(_.find(_this.deviceInterface.characteristics, ['characteristic', characteristicUUID]), 'name'));
            setTimeout(function () { return _this.alertOk(JSON.stringify(done)); }, 200);
        }, this.fail);
    };
