    UpdatePenComponent.prototype.onData = function (data, address, item, rawData) {
        var _this = this;
        // alert('onData: ' + JSON.stringify(data, null, 2));
        if (data && data.idx && data.idx.length && data.idx[1] === 0) {
            this.rawData = [];
        }
        for (var prop in data.data) {
            this.rawData.push(data.data[prop]);
        }
        this.successData = _.clone(this.rawData);
        if (data && data.idx && data.idx.length && data.idx[0] === 128) {
            var log = (new Date()).toISOString() + ' onData DONE (file receive success).';
            this.logData.push(log);
            // alert('DONE!!!!!!!!' + JSON.stringify(this.logData));
            var dataDone_1 = _.clone(rawData);
            dataDone_1[1] = 5;
            this._ble.write(address, { serviceUUID: item.serviceUUID, characteristicUUID: item.characteristicUUID, type: item.type }, dataDone_1, function (done) {
                var resp = {};
                switch (dataDone_1[0]) {
                    case 1:
                        // this.decodeBuffer(this.rawData, 'usageList');
                        // alert('usageList this.rawData: ' + JSON.stringify(this.rawData, null, 2));
                        resp = _this._pen.decodeBuffer(_this.rawData, 'usageList');
                        // alert('usageList resp: ' + JSON.stringify(resp, null, 2));
                        break;
                    case 2:
                        // this.decodeBuffer(this.rawData, 'errorList');
                        resp = _this._pen.decodeBuffer(_this.rawData, 'errorList');
                        break;
                    case 3:
                        resp = _this._pen.decodeBuffer(_this.rawData, 'blackList');
                        break;
                    case 5:
                        resp = _this._pen.decodeBuffer(_this.rawData, 'settings');
                        break;
                    default:
                        break;
                }
                alert('decodeBuffer resp: ' + JSON.stringify(resp, null, 2));
                _this.logData.push(resp.log);
                _this.decodedData = _.clone(resp.result);
                _this.successData = JSON.stringify(_this.decodedData, null, 2);
                _this.success(done, 'DONE ' + JSON.stringify(dataDone_1));
                _this.logData.push('--------------------------------------');
                _this.rawData = [];
                setTimeout(function () { return _this.alertOk('Done'); }, 200);
            }, this.fail);
        }
        // const cartIdArr: any = new Uint8Array(buffer, 2, 3);
        // const cartId: any = (cartIdArr[0] << 16) | (cartIdArr[1] << 8) | cartIdArr[2];
    };
    UpdatePenComponent.prototype.onDataErr = function (address, uuid, err) {
        // alert('onDataErr: ' + JSON.stringify(err));
        var log = (new Date()).toISOString() + ' onDataErr: ' + JSON.stringify(err, null, 2);
        this.logData.push(log);
        this._ble.stopNotification(address, uuid, this.success, this.fail);
    };
